import React, {Component} from 'react';
import "../styles/Work.scss";
import {Slide,Fade} from "react-reveal";
import Workdata from "../data/WorkObjectData";
import { CSSTransition, TransitionGroup} from 'react-transition-group';

class Work extends Component {

    render() {
        const {WorkProperties, WorkAutoplay} = this.props;
        let workObject3;
        let Description;
        if(WorkProperties.ImageUrl4 !== undefined) {
            workObject3 = <div className="grid__el grid__el-large" style={{backgroundImage: `url(${require(`../images/${WorkProperties.ImageUrl4}`)})`}}></div>
        } else {
            workObject3 = <div className="grid__el grid__el-large">
                            <video className="grid__el-video" autoPlay={WorkAutoplay} muted loop controls={true}>
                                <source src={require(`../images/${WorkProperties.VideoUrl1}`)} type='video/mp4' />
                            </video>
                          </div>
        }
        if (WorkProperties.File !== undefined ) {
            let file = WorkProperties.File
            Description = <p className="headline__description">{WorkProperties.description} <a className="headline__anchor"href={`${require(`../data/${file.fileUrl}`)}`} download>Click to download {file.fileName}.</a>
            </p>
        } else {
            Description = <p className="headline__description">{WorkProperties.description}</p>
        }
        return(
            <section className="Work page-section">
                <div className="wrapper">
                    <Slide bottom cascade>    
                        <h2 className="headline">My Work</h2>
                        <hr className="headline__styling-breakbar"/>
                    </Slide>
                    <Fade>
                        <div className="headline__buttons">
                        <button className={"btn btn--dark btn--small headline__button-left" + (WorkProperties.index === 0 ? " headline__button-disabled" : "")} onClick={this.props.onPrev} disabled={WorkProperties.index === 0}>Prev</button>
                        <button className={"btn btn--dark btn--small headline__button-right" + (WorkProperties.index === Workdata.properties.length-1 ? " headline__button-disabled" : "")} onClick={this.props.onNext} disabled={WorkProperties.index === Workdata.properties.length-1}>Next</button>
                        </div>
                    </Fade>
                    <TransitionGroup>
                       <CSSTransition
                           key={WorkProperties._id}
                           classNames="Work__fade"
                           timeout={500}
                           >
                           <div>
                                    <p className="headline__title headline__title--has-buttons">{WorkProperties.title}</p>
                                    <Slide bottom fraction={0.1}>
                                            <div>
                                            {Description}
                                            <div className="grid">
                                                <div className="grid__el grid__el-small" style={{backgroundImage: `url(${require(`../images/${WorkProperties.ImageUrl1}`)})`}}></div>
                                                <div className="grid__el grid__el-large" style={{backgroundImage: `url(${require(`../images/${WorkProperties.ImageUrl2}`)})`}}></div>
                                                {workObject3}
                                                <div className="grid__el grid__el-small" style={{backgroundImage: `url(${require(`../images/${WorkProperties.ImageUrl3}`)})`}}></div>
                                            </div>
                                            </div>
                                    </Slide>
                           </div>
                       </CSSTransition>
                      </TransitionGroup>
                </div>
            </section>
        )
    }
}

export default Work;