import React, { Component } from 'react'
import {Slide} from "react-reveal";
import "../styles/Stats.scss";
import * as d3 from "d3";

class Stats extends Component {
    // constructor(props) {
    //     super(props)
    // }

    componentDidMount() {
        const nodes = [
            {concept: "Front-End Skills", description: "Mobile first responsive web design", color: "white", size:8},
            {concept: "Automation", description: "Gulp (Incorporate Gulp-watch and browser-sync to start with, then pipe the autoprefixer, normalize, postcss, and etc in.), or just use Create-React-App.", color: "white", size:5},
            {concept: "Organisation", description: "Use tools like Version Control, Package Manager, and Webpack. Adapt methodologies like BEM and Composition of components.", color: "white", size:5},
            {concept: "Animation", description: "CSS transitions and animations, or Bodymovin library for advanced animations.", color: "white", size:5},
            {concept: "Optimization", description: "Code-splitting and Lazy-load are essential. Babel, Spirit SVG, etc are applied circumstantially.", color: "white", size:5},
            {concept: "Hobbies", description: "basketball, guitar, piano", color: "white", size:8},
            {concept: "Back-End Skills", description: "C#, SQL, and Node", color: "white", size:8},
            {concept: "Personal Info", description: "Age: 23, Birth-Day: 1996 Jan 6th, Nationality: Chinese", color: "white", size:8}
        ]

        const links = [
            {source: "Front-End Skills", target: "Automation"},
            {source: "Front-End Skills", target: "Organisation"},
            {source: "Front-End Skills", target: "Animation"},
            {source: "Front-End Skills", target: "Optimization"}
        ]
    
        // .attr("preserveAspectRatio", "xMinYMin meet")
        //  const containerAttributes = element.getBoundingClientRect();
         const svg = d3.select(".Stats__SVG").attr("viewBox", "0 0 400 400")

         const linkSelection = svg.selectAll("line")
                                  .data(links)
                                  .enter()
                                  .append("line")
                                    .attr("stroke", "black")
                                    .attr("stroke-width", 1)

         const nodeSelection = svg.selectAll("circle")
                                  .data(nodes)
                                  .enter()
                                  .append("circle")
                                    .attr("r", d => d.size)
                                    .attr("fill", d => d.color)
                                    .attr("stroke", "black")
                                    .attr("stroke-width", 3)
                                    .attr("class", "Stats__SVG__circle")
                                    .call(d3.drag()
                                            .on("start", (d) => {
                                                simulation.alphaTarget(0.5).restart()
                                                d.fx = d.x
                                                d.fy = d.y
                                            })
                                            .on("drag", (d) => {
                                                // simulation.alpha(0.5).restart()
                                                d.fx = d3.event.x
                                                d.fy = d3.event.y
                                            })
                                            .on("end", (d) => {
                                                simulation.alphaTarget(0)
                                                d.fx = null
                                                d.fy = null
                                            }))
                                    .on("mousemove touchmove", this.showTooltip)
                                    .on("mouseout touchend", this.hideTooltip);

         const simulation = d3.forceSimulation(nodes)
         simulation.force('center', d3.forceCenter(200,200))
                   .force('nodes', d3.forceManyBody().strength(-18))
                   .force('links', d3.forceLink(links)
                                     .id(d => d.concept)
                                     .distance(d => 5 * (d.source.size + d.target.size)))
                   .on('tick', () => {
                       nodeSelection
                        .attr('cx', d => d.x)
                        .attr('cy', d => d.y);
                    
                       linkSelection   
                        .attr("x1", d => d.source.x)
                        .attr("y1", d => d.source.y)
                        .attr("x2", d => d.target.x)
                        .attr("y2", d => d.target.y)
                   });
         this.setState({sim: simulation})

    }

    showTooltip(d) {
        var tooltip = d3.select(".tooltip");
        // console.log(d3.event)
        console.log(tooltip.node().offsetWidth)
        tooltip
            .style("opacity", 1)
            .style("left", '50%')
            .style("top", '1%')
            .style("transform", 'translateX(-50%)')
            .html(() => {
              const description = (d.description === "" ? "" : `<p>${d.description}</p>`)
              return `
                <p><b>${d.concept}</b></p>
                ${description}
              `
            });
    }
    
    hideTooltip() {
        d3.select(".tooltip")
            .style("opacity", 0);
    }

    render() {
        return (
            <section className="page-section">
                <div className="wrapper">
                    <Slide bottom cascade>    
                        <h2 className="headline">My Stats</h2>
                        <hr className="headline__styling-breakbar"/>
                    </Slide>
                    <div className="Stats__SVG__Container">
                        <div className="tooltip"/>
                        <svg className="Stats__SVG">
                        </svg>
                    </div>
                </div>
            </section>
        )
    }
}

export default Stats;