import React, { Component } from 'react';
import Waypoint from 'react-waypoint';
import { Element } from 'react-scroll'

class Portkey extends Component {
      
    handleWaypointStartingLineStart = ({currentPosition}) => {
        if(currentPosition === 'above') {
            this.props.setLinksIsCurrentPosition(this.props.after)
            this.props.toggleHeaderSlide(true,false)
        }
    }
    handleWaypointEndingLineLeave = ({currentPosition}) => {
        if(currentPosition === 'above') {
            this.props.toggleHeaderSlide(false,true)
        }
    }
    handleWaypointEndingLineStart = ({previousPosition}) => {
        if(previousPosition === 'above') {
           this.props.toggleHeaderSlide(true,false)
        }
    }
    handleWaypointStartingLineLeave = (previousPosition) => {
        if(previousPosition.previousPosition === 'above') {
            this.props.setLinksIsCurrentPosition(this.props.before)
            this.props.toggleHeaderSlide(false,true)
        }
    }

    handleFirstWaypointStartingLineLeave = ({previousPosition}) => {
        if(previousPosition === 'above') {
            this.props.setLinksIsCurrentPosition(this.props.before)
            this.props.toggleHeaderSlide(false,false)
            this.props.toggleHeaderIsBackToTop(true)
        }
    }
    
    render() {
        const {first,id} = this.props;
        return(
            <div className="PortKey">
                 <Waypoint
                    onEnter={(first ? this.handleFirstWaypointStartingLineLeave : this.handleWaypointStartingLineLeave)}
                    onLeave={this.handleWaypointStartingLineStart}>
                </Waypoint>
                    <div style={{width:'100%', height: '10px', backgroundColor:'black'}}/> 
                    <Element name={id}/>
                <Waypoint
                    onEnter={this.handleWaypointEndingLineStart}
                    onLeave={this.handleWaypointEndingLineLeave}>
                    <div style={{position: 'absolute', width:'100%', height: '50px'}}> </div>
                </Waypoint>
            </div>
        )
    }
}

export default Portkey;