import React, { Component } from 'react'; 
import Modal from './Modal';
import LetterForm from './LetterForm'; 

class ContactModal extends Component {
    constructor(props) {
        super(props);
        this.state={
            LetterFormIsVisible: false,
        };
    }

    toggleLetterForm = (boolean) => {
        this.setState({LetterFormIsVisible: boolean})
    }
    
    render() {
        const {ContactModalIsOpen} = this.props;
        const {LetterFormIsVisible} = this.state;
        return(
            <Modal toggleModal= {this.props.toggleContactModal} ModalIsOpen={ContactModalIsOpen}> 
                <h2 className="headline">My Contact</h2>
                <hr className="headline__styling-breakbar"/>
                <p className={"headline__description" + (LetterFormIsVisible ? " headline__description-is-hide": " headline__description-is-shown")}> I can be reached at <b>+86 15546131588</b> or by email, skype, or an owl. Details are down below!<br/>
                    Email: <b>liyinduo@gmail.com</b><br />
                    Skype: <b>mySkypeId</b> <br />
                    Send a letter through my owl: <br /> <button className="btn btn--long btn--dark" onClick={()=>(this.toggleLetterForm(true))}>Send a letter</button>
                </p>
                <LetterForm LetterFormIsVisible={LetterFormIsVisible} toggleLetterForm={this.toggleLetterForm} RequiredFields={['Name', 'Email', 'Message']}/>
            </Modal>
        )
    }

}

export default ContactModal;