import React, { Component } from 'react'; 
import Modal from './Modal';
import LetterForm from './LetterForm'; 

class FeedbackModal extends Component {
    constructor(props) {
        super(props);
        this.state={
            LetterFormIsVisible: false,
        };
    }

    toggleLetterForm = (boolean) => {
        this.setState({LetterFormIsVisible: boolean})
    }
    
    render() {
        const {FeedbackModalIsOpen} = this.props;
        const {LetterFormIsVisible} = this.state;
        return(
            <Modal toggleModal= {this.props.toggleFeedbackModal} ModalIsOpen={FeedbackModalIsOpen}> 
                <h2 className="headline">Feedback Form</h2>
                <hr className="headline__styling-breakbar"/>
                <p className={"headline__description" + (LetterFormIsVisible ? " headline__description-is-hide": " headline__description-is-shown")}> 
                    Thank you for deciding to send me a feedback; I truly appreciate it. <br/>
                    Please use the form below to send me a message.<br/>
                    Because there might be cases when you wish to leave a message anonymously, <br/>I only set the message field as a required field.<br/><br/>
                    <button className="btn btn--long btn--dark" onClick={()=>(this.toggleLetterForm(true))}>Send a feedback</button>
                </p>
                <LetterForm LetterFormIsVisible={LetterFormIsVisible} toggleLetterForm={this.toggleLetterForm} RequiredFields={['Message']}/>
            </Modal>
        )
    }

}

export default FeedbackModal;